import { FunctionComponent, useEffect, useState } from 'preact/compat';
import { useText } from '@adobe/elsie/i18n';
import { events } from '@adobe/event-bus';
import { AlertBanner, AlertBannerProps, Icon } from '@adobe/elsie/components';
import { WarningFilled } from '@adobe/elsie/icons';

export const ErrorBanner: FunctionComponent<Partial<AlertBannerProps>> = (
  props
) => {
  const [hasError, setHasError] = useState<boolean>(false);

  useEffect(() => {
    const subscription = events.on('error', (data) => {
      if (data?.source === 'checkout' && data?.type === 'network') {
        setHasError(true);
      }
    });
    return () => {
      subscription?.off();
    };
  }, []);

  const onDismiss = () => {
    setHasError(false);
  };

  const translations = useText({
    message: 'Checkout.ErrorBanner.genericMessage',
  });

  if (!hasError) return null;

  return (
    <AlertBanner
      {...props}
      className="checkout__banner"
      data-testid="error-banner"
      icon={<Icon source={WarningFilled} />}
      message={<span>{translations.message}</span>}
      onDismiss={onDismiss}
      variant="warning"
    />
  );
};
